.card-heading {
  text-align: left;
  font-weight: bold;
  font-size: 30px;
}

.project {
  max-width: fixed;
  display: flex;
}

.project-card {
  margin-top: 10px;
  margin-bottom: 10px;
  box-sizing: border-box;
  display: inline-block;
  width: 250px;

  margin-right: 5px;
  margin-left: 5px;
  border-radius: 10px;
  background-color: #efefef;
  /* border: 2px solid #000; */
}

.project-card:hover {
  transform: scale(1.02);
  transition: transform 0.1s ease-in-out;
}

.slide-container {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  position: relative;
  background-color: #f7f9fb;
}

.slide-content {
  margin: 0 4px;
}

.image-content,
.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 14px;
}

.image-content {
  background-color: #0670a7;
  border-radius: 8px 8px 0 0;
}
.card-image {
  position: relative;
  height: 100px;
  width: 100px;
  background: #fff;
  padding: 3px;
}

.image-content {
  position: relative;
  row-gap: 5px;
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  border-radius: 25px 25px 0 25px;
}

.card-image .card-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.project-name {
  color: #333;
}

.button {
  border: none;
  font-size: 16px;
  color: #fff;
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #333;
  margin: 14px;
  transition: all 0.3s ease;
}

.button:hover {
  background-color: #545353;
}

.slider {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  white-space: nowrap;
  scrollbar-width: thin;
  scrollbar-color: #c1c1c1 #e8e8e8;
}

.slider::-webkit-scrollbar {
  height: 8px;
}

.slider::-webkit-scrollbar-track {
  background: #e8e8e8;
}

.slider::-webkit-scrollbar-thumb {
  background-color: #c1c1c1;
  border-radius: 20px;
  border: 3px solid #e8e8e8;
}

.left,
.right {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  cursor: pointer;
  opacity: 0.5;
}

.left {
  left: 10px;
}

.right {
  right: 10px;
}

.course-card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding-left: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
}

.course-info h3 {
  margin-bottom: 5px;
}

.course-info p {
  font-size: 14px;
  color: #666;
  margin-bottom: 5px;
}

.tech-stack {
  display: inline-block; /* Aligns the content in line and allows us to set a width and height */
  font-size: 12px;
  color: #666;
  padding: 5px 15px; /* Adds space inside the box: 5px top/bottom, 15px left/right */
  margin: 5px 0; /* Optional: Adds space outside the box, for better spacing from other elements */
  background-color: #e9e9e9; /* Gives a background color to the box */
  border-radius: 20px; /* Creates the oval shape */
  border: 1px solid #bbb; /* Adds a border for better definition, optional */
}

.course-stats {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.upvotes,
.downvotes {
  margin-right: 10px;
  font-size: 14px;
}

.course-review {
  font-size: 16px;
  color: #666;
  margin-top: 5px;
}

.course-title-author {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.course-title-author h3,
.course-title-author p {
  margin: 0; /* Removes default margin */
}

.course-title-author span {
  margin: 0 10px;
  color: #666;
}

.buttons {
  margin-top: 5px;
  display: flex;
  justify-content: space-around;
}

.collaboration-req {
  border: 1px solid black;
  padding: 5px;
  border-radius: 10px;
  justify-content: center;
}
.collab-user {
  font-weight: bold;
  font-size: 18px;
}

.collab-decline-button,
.collab-accept-button {
  border: none;
  padding: 5px 10px;
  border-radius: 20px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.collab-decline-button {
  background-color: #ff6b81;
}

.collab-accept-button {
  background-color: #4caf50;
}

@media (max-width: 400px) {
  .profile-container {
    flex-direction: column;
  }
  .profile-left {
    flex-direction: column;
  }
  .profile-info {
    flex-direction: column;
  }
}

  .collaboration-req{
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .collab-card{
    border: 1px solid black;
    padding: 5px;
    border-radius: 10px;
    justify-content: center;
  }
  .collab-user{
    font-weight: bold;
    font-size: 18px;
  }

  
  .collab-decline-button,
  .collab-accept-button {
    border: none; 
    padding: 5px 10px; 
    border-radius: 20px; 
    color: white; 
    font-weight: bold;
    cursor: pointer; 
  }
  
  .collab-decline-button {
    background-color: #ff6b81; 
  }
  
  .collab-accept-button {
    background-color: #4caf50;
  }

  .Maincontent-button-toggle{
    visibility: hidden;
    /* display: block; */
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: transparent; 
    border: none;
    padding: 10px;
    cursor: pointer;
  }
  .Maincontent-sidebar{
    flex:1;
    padding-right: 4%;
  }
  @media only screen and (max-width: 900px) {
    .Maincontent-sidebar {
      display: block;
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      width: 300px;
      background-color: #f0f0f0;
      transition: transform 0.3s ease-in-out;
      z-index: 2;
      overflow: scroll;
    }
    .Maincontent-button-toggle{
      visibility: visible;
      z-index: 3;
    }
  }